import {useContext} from 'react';
import { ScrapeContext} from './scrapeContext';

const Data = ()=> {
  const {scrapes} = useContext(ScrapeContext)
  return (<div>
    <h2>Top 100 Profiles sorted by number of Followers</h2>
    <table>
      <thead>
        <th>InstagramId</th>
        <th>Name</th>
        <th>Number of followers</th>
        <th>Dreister Follower?</th>
      </thead>
      <tbody>

      </tbody>
    </table>
    <ul>
      {scrapes.map(scrape => <li key={scrape.instagramId}>{scrape.instagramId} - {scrape.nbFollowers} Followers - {scrape.isFollowingDreister ? 'Already following Dreister':'Not following Dreister'}</li>)}
    </ul>
  </div>)
}

export default Data;
